import React from 'react';
import {
    Modal,
    Button,
} from "antd";
import './index.less';

/**
 * SelectDataModal
 * @param props
 *          visible: falsetrue
 *          title: modal
 *          content: 
 *          extraFooter: modal
 *          onOk: 
 *          onCancel: x
 * @constructor
 */
function Index(props) {

    return (
        <Modal
            visible={props.visible}
            title={props.title || 'modal'}
            className={'modal'}
            width={1130}
            onCancel={() => {
                const { onCancel } = props;
                onCancel && onCancel();
            }}
            footer={
                <div>
                    {props.extraFooter || ''}
                    <Button onClick={() => {
                        const { onCancel } = props;
                        onCancel && onCancel();
                    }}></Button>
                    <Button
                        type="primary"
                        // disabled={selectedRowKeys.length === 0}
                        onClick={() => {
                            const { onOk } = props;
                            onOk && onOk();
                        }}
                    >
                        
                    </Button>
                </div>
            }
        >
            {
                props.content
            }
        </Modal>
    );
}

export default Index;



